                <p class="header-tip">我是{{ nickname }}，邀请你一起来想看赚钱，你完成新手任务，咱俩都有钱赚！</p>
            </div>
        </div>
        <div class="guide">
            <p>1、当日下载登录并绑定邀请码，获得¥1.3+¥0.1元现金，前5天每天都可提现。</p>
            <div class="rouse-money">
                <div>
                    <p>送 <span>1.3</span> 元</p>
                    <p>官方新用户红包</p>
                </div>
                <div>
                    <p>送 <span>0.1</span> 元</p>
                    <p>绑定成为我的好友</p>
                </div>
            </div>
            <p>2、在赚金币页面完成所有新手任务，再得¥3元。</p>
            <div class="rouse-money">
                <div>
                    <p>送 <span>1</span> 元</p>
                    <p>阅读，领时段金币</p>
                </div>
                <div>
                    <p>奖 <span>2</span> 元</p>
                    <p>连续3天看3条内容</p>
                </div>
            </div>
        </div>
        <div
            @click="download"
            class="btn">去APP内完成</div>
    </div>
</template>

<script>
import { invitationShare } from '@/api/invitation';
import { goApp } from '@/utils/share';

export default {
    name: 'InvitationRouse',
    data() {
        return {
            nickname: '',
            headicon: '',
            code: this.$route.query.shareId,
        };
    },
    created() {
        window.document.title = '跟我一起领红包！- 想看资讯 - 想看世界，想看你！';

        invitationShare({ invitationCode: this.code }).then((res) => {
            if (res.data.status === 200) {
                this.headicon = res.data.data.headIcon;
                this.nickname = res.data.data.user;
                this.$root.hidePlaceholder();
            } else {
                this.$root.hidePlaceholder();
                this.$error(this.$route.name);
            }
        }).catch(() => {
            this.$root.hidePlaceholder();
            this.$error(this.$route.name);
        });
        this.$o2o({
            e_c: '分享页',
            e_a: '曝光',
            e_n: '邀请码提醒好友',
        });
    },
    methods: {
        download() {
            this.$o2o({
                e_c: '分享页',
                e_a: '点击',
                e_n: '挑战8元奖励',
            });
            goApp(
                'bikan://goto/mineTab',
                `${window.location.origin}/api/v1/package/xiangkan_invitation.apk`,
                { ckey: 'CK1429645648411' },
            );
        },
    },
};
</script>

